<script>
   /*************************************************************/
   /* Root component for the application                        */
   /*************************************************************/

   import { date2str } from '../../shared/utils';

   import CreateAppContainer from './tab_create/AppContainer.svelte';
   import ApplyAppContainer from './tab_apply/AppContainer.svelte';
   import App from '../../shared/components/MainApp.svelte';
   import WASM from '../../shared/wasm/dist/prep_wasm.js';


   /* callback to check validity of JSON with session */
   function checkSession(s) {
      const keys1 = Object.keys(s);
      if (!['date', 'tab', 'create', 'apply'].every(v => keys1.includes(v))) return false;

      const keys2 = Object.keys(s.create);
      if (!['rawdata', 'prepdata', 'rawdataShort', 'prepdataShort',
         'prepmodel', 'left', 'right', 'varind'].every(v => keys2.includes(v))) return false;
      return true;
   }

   /* callback to initialize new session */
   function initSession() {
      return {
         date: date2str(new Date()),
         tab: 0,
         create: {
            rawdata : 0,
            prepdata: 0,
            rawdataShort: 0,
            prepdataShort: 0,
            prepmodel: 0,
            prepobj: [],
            prepmeta: 0,
            left: 0,
            right: 0,
            varind: [-1, -1, -1]
         },
         apply: {
            rawdata : 0,
            prepdata: 0,
            model: 0,
            left: 0,
            right: 0,
            varind: [-1, -1, -1]
         }
      }
   }

   // app containers
   const tabs = [
      {name: 'Create', label: 'create', component: CreateAppContainer},
      {name: 'Apply', label: 'apply', component: ApplyAppContainer},
   ]


</script>

<App
   appID="prep"
   appTitle="Preprocessing"
   appShortTitle="Preprocessing"
   appSlogan="Make your data great again"
   {WASM}
   {tabs}
   {initSession}
   {checkSession}
>

   {#snippet footer()}
      <p>Current release: <span>1.2.1</span>
      Feedback: <a class="paper-link" href="mailto:help@mda.tools?subject=Preprocessing app">help@mda.tools</a>
      | <a class="paper-link" href="/prep/references.pdf" download>references</a>
      | <a href="https://www.youtube.com/playlist?list=PLep7KM9cEyFmesy9tzG9OzOcSCFigU5hh">video lessons</a>
      </p>
   {/snippet}

   {#snippet info()}
   <h2>Data preprocessing</h2>
   <p>
      Interactive web-application for data preprocessing. Most of the methods it contains are
      applicable for different kinds of spectra. The selected chain of methods and their parameters can be saved as preprocessing model
      and be applied later to another dataset or incorporated to other methods (PCA, PLS, etc.).
   </p>
   <h3>v. 1.2.1</h3>
   <p class="release-info">released 04/09/2025</p>
   <ul>
      <li>Small improvements to session mechanism.</li>
   </ul>
   <h3>v. 1.2.0</h3>
   <p class="release-info">released 21/08/2025</p>
   <ul>
      <li>Fixed small bug in Apply tab plot.</li>
      <li>Improvements to UI/UX in creator.</li>
      <li>Added more references to methods description.</li>
      <li>Added link to video tutorial.</li>
   </ul>
   {/snippet}
</App>

